<template>
    <div class="invoice">
      <!---->
      <van-nav-bar
        title=""
        left-text=" "
        arrow-left
        left-arrow
        @click-left="onClickLeft"
      />
  
      <div>
        <img src="data:image/png;base64," alt="">
        <p></p>
      </div>
    </div>
</template>

<script>
    export default {
        name: "invoice",
      methods:{
        onClickLeft(){
          this.$router.go(-1);
        }
      },
    }
</script>

<style scoped>
  .van-nav-bar {
    background-color: #3190e8;
  }
  .van-nav-bar__title {
    color: white;
    font-weight: 400;
  }
  :before{
    color: white;
    text-align: center;
  }
  div{
    text-align: center;
  }
  img{
    width: 8rem;
    height: 5rem;
    margin-top: 5rem;
  }
  p{
    font-size: .6rem;
    color: #999;
    margin-top: .8rem;
  }
</style>



