<template>
    <div class="search">
      <div class="s-header clear">
        <i class="iconfont txtQ_c" @click="btnReturnB_c">&#xe606;</i>
        <span></span>
        <div class="empty"></div>
      </div>
      <div class="searchM_c">
      <form action="">
        <input type="search" placeholder="//" class="searchQ_c" v-model="searchQ_C">
        <button class="btnSearch_C" @click.stop.prevent="btnSearchA_c"></button>
      </form>
      <ul>
        <!--<router-link :to="{path:'/site',query:{textN_c:pro.name}}" v-for="(pro, index) in searchCity_c" :key="index">-->
        <li v-for="(pro, index) in searchCity_c" :key="index"  @click="btnText_c(pro.name)">
          <p class="textL_c">{{pro.name}}</p>
          <p class="textK_c">{{pro.address}}</p>
        </li>
        <!--</router-link>-->
      </ul>
      </div>
      <div class="boxA_c" v-if="textM_c">
        <p> ?</p>
        <p></p>
        <p></p>
      </div>
    </div>
</template>

<script>
  import {mapActions,mapState} from "vuex"
  import {getSearchCity} from "../../serivice/api";
    export default {
        name: "searchA_c",
      data(){
          return {
            searchQ_C:"",  //
            cityA_c:1,   //id
            searchCity_c:[],   //
            textM_c:true,
            textN_c:"",    //

          }
      },
        methods: {
          ...mapActions(['SET_SEARCH_ADDRESS_NAME','GET_CITY']),
          btnReturnB_c() {
            this.$router.go(-1);  //
          },
          btnSearchA_c() {
            console.log(1);
            let params_c = {city_id: Number(this.cityA_c), keyword: this.searchQ_C, type: 'search'};
            getSearchCity(params_c).then((result) => {
              this.searchCity_c = result;
              console.log(result);
              // this.textN_c = this.searchCity_c.name
              // console.log(this.textN_c);
              if(result.name == "ERROR_QUERY_TYPE"){
                this.textM_c = true;
              }else{
                this.textM_c = false;
              }
              console.log(this.searchCity_c);
            })
          },
          btnData(){

          },
          btnText_c(p){
            this.SET_SEARCH_ADDRESS_NAME(p);
            this.$router.go(-1);
            // this.$store.dispatch('setsearchAddressName',p)
          }
        }
    }
</script>

<style scoped lang="less">
.search {
  width: 100%;
  height: 100%;
  background: white;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
.s-header {
  background-color: #3190e8;
  width: 100%;
  height: 1.95rem;
  color: white;
  font-size: 0.8rem;
  line-height: 1.95rem;
  text-align: center;
.txtQ_c {
  float: left;
  margin-left: 0.5rem;
}
span {
  margin-right: 0.9rem;
  font-weight: bold;
}
}
  .searchM_c{
    height: 93%;
    /*background: red;*/
  }
  .searchQ_c {
    font-size: 0.7rem;
    width: 70%;
    height: 1.5rem;
    margin: 0.7rem 0.7rem;
    padding: 0 0.4rem;
    background: #F5F5F5;
    border-radius: 0.2rem;
  }
  .btnSearch_C {
    background: #3190e8;
    color: white;
    font-size: 0.7rem;
    padding: 0.3rem 0.6rem;
    border-radius: 0.2rem;
  }
  ul {
    width: 100%;
    height: 90%;
    padding: 0.5rem 0.6rem;
    overflow: auto;
    li {
      width: 100%;
      padding: 0.7rem 0;
      .textL_c {
        font-size: 0.8rem;
        color: #555;
      }
      .textK_c {
        font-size: 0.7rem;
        color: #999;
      }
    }
  }
  .boxA_c {
    width: 100%;
    font-size: 0.5rem;
    text-align: center;
    position: absolute;
    top: 13rem;
    color: #aaa;
  }
}
.iconfont{
  font-family:"iconfont" !important;
  font-size:16px;font-style:normal;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale;}
</style>



