<template>
    <div class="redact">
      <van-nav-bar
        title=""
        left-arrow
        @click-left="onClickLeft"
      />
      <div class="all">
        <!---->
        <div class="div1">
          <input type="text" placeholder="" @input="yourN" v-model="name" :class="{input:cheack}">
          <p v-if="cheack"></p>
        </div>
        
        <!---->
        <router-link :to="{path:'/add'}">
          <div class="div2">
            <input type="text" placeholder="//" v-model="all.name" style="color: black">
          </div>
        </router-link>

        <!---->
        <div class="div3">
          <input type="text" placeholder="" v-model="addRes" :class="{input:cheack1}" @input="address">
          <p v-if="cheack1"></p>
          <p v-if="cheack2"></p>
        </div>

        <!---->
        <div class="div4">
          <input type="text" placeholder="" v-model="myPhone" :class="{input:cheack3}" @input="newPhone">
          <p v-if="cheack3"></p>
          <p v-if="cheack4"></p>
        </div>

        <!---->
        <div class="div5">
          <input type="text" placeholder="" v-model="bPhone" @input="newBphone">
          <p v-if="cheack5"></p>
        </div>
      </div>

      <!---->
        <div class="div6">
          <button @click="btnAdd"></button>
        </div>
      <!---->
      <transition name="fade" enter-active-class="animated bounceIn"
                  :duration="400">
        <Pop_c v-if="show" :popKuang="popKuang"></Pop_c>
      </transition>

    </div>
</template>

<script>
  import {mapActions,mapState} from "vuex"
  import {postadd} from "../serivice/api"
  import Pop_c from "../components/Cpm_c/Pop_c"
    export default {
        name: "redact",
        components:{Pop_c},
      data(){
          return{
             cheack:false,
            cheack1:false,
            cheack2:false,
            cheack3:false,
            cheack4:false,
            cheack5:false,
            //
            name:"",
            //
            addRes:"",
          // 
            myPhone:"",
          //  
            bPhone:"",
          //  
            msg:[],
            allinfo:[],
            show:false,
            popKuang:"",
          }
      },
      methods:{
        ...mapActions(['SET_USER_INFO']),
        ...mapActions(['SET_ALL_INFO']),
        btntxt_c(show){
          this.show = show;
        },
        onClickLeft(){
          this.$router.push({path:'/site'});
        },
      //  
        yourN(){
          if(this.name==""){
            this.cheack=true;
          }else{
            this.cheack=false;
          }
        },
      //
        address(){
          if(this.addRes.length==0){
            this.cheack1=true;
            this.cheack2=false;
            return;
          }else if(this.addRes.length<3 ){
            this.cheack1=false;
            this.cheack2=true;
            return;
          }else if(this.addRes.length>=3){
            this.cheack1=false;
            this.cheack2=false;
            return;
          }
        },
        newPhone(){
          if(this.myPhone.length==0){
            this.cheack3=true;
            this.cheack4=false;
            return;
          }else if(!(/^(13[0-9]|14[0-9]|15[0-9]|166|17[0-9]|18[0-9]|19[8|9])\d{8}$/).test(this.myPhone)){
            this.cheack4=true;
            this.cheack3=false;
            return;
          }else {
            this.cheack4=false;
            this.cheack3=false;
            return;
          }
        },

        newBphone(){
          if (this.bPhone.length==0) {
            this.cheack5=false;
            return;
          }else if(!(/^(13[0-9]|14[0-9]|15[0-9]|166|17[0-9]|18[0-9]|19[8|9])\d{8}$/).test(this.bPhone)){
            this.cheack5=true;
            return;
          }else if(this.bPhone.length>11){
            this.cheack5=true;
            return;
          }else{
            this.cheack5=false;
            return;

          }
        },

      //  
        btnAdd(){

          postadd(this.userinfo.user_id,{
              address_detail:this.all.name,
              address:this.addRes,
              geohash:this.all.geohash,
              name:this.name,
              phone:this.myPhone,
              tag :"",
              sex :1,
              phone_bk:this.bPhone,
              tag_type:2
           }
          ).then((result)=>{
              console.log(result)
              //
            if(result.message == ""){
              this.show = true;
              this.popKuang = "";
              return;
            }

            if(result.message == ""){
              this.show = true;
              this.popKuang = "";
              return;
            }
              //
            if(result.message == ""){
              this.show = true;
              this.popKuang = "";
              return;
            }
            //
            if(result.message == ""){
              this.show = true;
              this.popKuang = "";
              return;
            }
            //
            if(result.status == 1){
              // this.show = true;
              // this.popKuang = "";
              // return;
              this.SET_ALL_INFO([]);
              this.$router.push({path:'/site'})
            }
          });
        }
      },
      // mounted(){
      // //
      //   this.msg=JSON.parse(localStorage.getItem('all'));
      //   console.log(this.msg.name)
      //   // this.allinfo=JSON.parse(localStorage.getItem('allinfo'));
      //   // console.log(this.allinfo)
      //
      // },
      computed:{
        ...mapState(['userinfo']),
        ...mapState(['all']),
      }
    }
</script>

<style scoped lang="less">
  .van-nav-bar {
    background-color: #3190e8;
  }
  .van-nav-bar__title {
    color: white;
    font-weight: 400;
  }
  :before{
    color: white;
    text-align: center;
  }
  .all{
    background: #fff;
    padding-top: .4rem;
  }
  div{
    padding-bottom: .4rem;
  }
  input{
    display: block;
    width: 15rem;
    font-size: .6rem;
    margin: 0 auto;
    padding: .3rem;
    background: #f2f2f2;
    border: 1px solid #ddd;
    border-radius: 3px;
  }
  .input{
    border-color: #ea3106;
  }
  p{
    font-size: .4rem;
    color: #ea3106;
    padding-left: .5rem;
    margin-top: .2rem;
  }
  .div6{
    margin: .6rem auto;
    width: 15rem;
    background: #4cd964;
    border-radius: 3px;
  }
  button{
    width: 100%;
    font-size: .6rem;
    color: #fff;
    line-height: 1.6rem;
    background: none;
    font-weight: 700;
    opacity: .6;
  }
</style>



