export default {
   // id   restaurant 
  shopcard: (state) => (id) => {
    var result =  state.restaurantgoods.filter(item=>{
      return  item.restaurant_id== id;
    });
    return result;
  },
  //
  allcategorynum: (state) => (id) => {
    var goods_num = [];
    var result =  state.restaurantgoods.filter(item=>{
      return  item.restaurant_id== id;
    });
    result.forEach(item=>{
      var num = 0;
      item.foods.forEach(it=>{
        it.specfoods.forEach(i=>{
          if(i.goodsnum>0){
            num  = i.goodsnum+num;
          }else{
            num=num+0;
          }
        });
      });
      goods_num.push(num)
    });
    return goods_num;
  },
  //
  allcartlist: (state) => (id) => {
    var cartlist = [];
    var result =  state.restaurantgoods.filter(item=>{
      return  item.restaurant_id== id;
    });

    result.forEach(item=>{
      item.foods.forEach(it=>{
          var type  =  it.specifications.length>0?'spec_type':'normal_type';
             it.specfoods.forEach(i=>{
               if(i.goodsnum>0){
                 i.type = type;
                 cartlist.push(i);
               }
             })

      });
    });
     return cartlist;
  }

}



