<template>
  <div class="account">
    <van-nav-bar
      title=""
      left-arrow
      @click-left="onClickLeft"
    />

    <div class="top">
      <input type="file">
      <h2></h2>
      <img src="//elm.cangdu.org/img/default.jpg" alt="" class="img1">
      <van-icon name="arrow" class="van1"/>
      <!---->
      <router-link :to="{path:'/amend'}">
        <span class="span1"></span>
        <span class="span2">{{userinfo.username}}</span>
        <van-icon name="arrow" class="van2"/>
      </router-link>
      <!---->
      <router-link :to="{path:'/site'}" class="a2">
        <span class="span1"></span>
        <van-icon name="arrow" class="van2 right"/>
      </router-link>
      <!---->
      <p></p>
      <button @click="phone">
        <img  class="img2" src="data:image/png;base64," alt="">
        <span></span>
        <van-icon name="arrow" class="van right"/>
      </button>
      <!---->
      <transition enter-active-class="animated bounceIn" leave-active-class="opacity 0">
        <div class="bottom" v-if="show">
          <img src="../imags/ios.png" alt="" class="van4">
          <h4 >APP</h4>
          <button class="btn3" @click="confirm"></button>
        </div>
      </transition>
      <!---->
      <p></p>
      <router-link :to="{path:'/Reset'}" class="a2">
        <span class="span1"></span>
        <span class="span2 span3"></span>
        <van-icon name="arrow" class="van2 right"/>
      </router-link>
      <!---->
      <div class="btn">
        <button class="btn1" @click="backdl"></button>
      </div>

    </div>
    <div class="bgc" v-if="show1"></div>
    <!---->
    <transition enter-active-class="animated bounceIn" leave-active-class="opacity 0">
      <div class=" bto" v-if="show1">
        <img src="../imags/ios.png" alt="" class="van4">
        <h1></h1>
        <button class="btn4" @click="wait"></button>
        <button class="btn5" @click="back" ></button>
      </div>
    </transition>

  </div>
</template>

<script>
  import {mapActions,mapState} from "vuex"
  import {getExit} from "../serivice/api"
  export default {
    name: "account",
    data(){
      return{
        show:false,
        show1:false,
        allinfo:[],
      }
    },
    methods:{
      ...mapActions(['SET_USER_INFO']),
        onClickLeft(){
          this.$router.push({path:'/minejmx'});
        },
      phone(){
        this.show=true;
      },
      confirm(){
        this.show=false;
      },
      backdl(){
        this.show1=true;
      },
      wait(){
        this.show1=false;
      },
      back(){
        getExit().then((result)=>{
          console.log(result)
          if (result.status==1){
            this.SET_USER_INFO({});
            this.$router.push({path:"/minejmx"});

          }
        })

      }
    },
    mounted(){
      this.allinfo=JSON.parse(localStorage.getItem('allinfo'))
    },
    computed: {
      ...mapState(['userinfo']),
    },
  }
</script>


<style scoped lang="less">
  .van-nav-bar {
    background-color: #3190e8;
  }
  .van-nav-bar__title {
    color: white;
    font-weight: 400;
  }
  :before{
    color: white;
    text-align: center;
  }
  .account{
    width: 100%;
    background-color: #f2f2f2;
    position: relative;
    .bgc{
      background-color: #000;
      position:absolute;
      top: 0;
      left: 0;
      bottom: -2.8rem;
      right: 0;
      z-index: 2;
      opacity: .2;
    }
    .bto{
      width: 96%;
      height:60%;
      background-color:white;
      position:absolute;
      left: 2%;
      right: 2%;
      top: 10%;
      z-index: 10;
      text-align: center;
      border-radius: .1rem;
      .van4{
        padding-top: .2rem;
        font-size: 2rem;
        color: #f8cb86;
        margin-top: .5rem;
      }
      h1{
        margin-top: .4rem;
      }
      .btn4{
        background: #c1c1c1;
        margin-right: .3rem;
        border-radius: .1rem;
        font-size: .5rem;
        color: #fff;
        width: 4rem;
        line-height: 1.5rem;
        letter-spacing: .05rem;
        margin-top: 1.3rem;
        /*padding: 0.3rem 3rem;*/
      }
      .btn5{
        background: #dd6b55;
        border-radius: .1rem;
        font-size: .3rem;
        color: #fff;
        width: 5rem;
        line-height: 1.5rem;
        letter-spacing: .05rem;
      }
    }
    .top{
      display: inline-block;
      width: 100%;
      margin-top: .4rem;
      position: relative;
      background-color: white;
      border-top: .25px solid #ddd;
      input{
        width: 100%;
        height:3rem;
        opacity: 0;
        display: block;
      }
      h2{
        font-size: .6rem;
        color: #333;
        font-weight: 500;
        position: absolute;
        top: 1.2rem;
        left: .3rem;
      }
      .img1{
        width: 2rem;
        /*height: .9rem;*/
        border-radius: 50%;
        position: absolute;
        top: .5rem;
        right: .6rem;
      }
      .van1{
        position: absolute;
        top: .6rem;
        right: .2rem;
        color: #e4e4e4;
        font-size: .4rem;
      }
      a{
        width: 100%;
        display: inline-block;
        border-top: .2px solid #ddd;
        padding: .5rem 0 .2rem .5rem;
        .span1{
          display: inline-block;
          font-size: .6rem;
          color: #333;
        }
        .span2{
          display: inline-block;
          float: right;
          margin-right: .8rem;
          color: #999;
          font-size: 0.7rem;
        }
        .van2{
          font-size: .6rem;
          color: #e4e4e4;
          padding-right: .2rem;
          vertical-align: .2rem;
        }
      }
      .a2{
        border-bottom: .2px solid #ddd;
      }
      p{
        background-color: #f2f2f2;
        padding: .3rem;
        font-size: .7rem;
        color: #666;
      }
      button{
        width: 100%;
        background-color: white;
        text-align: left;
        border-top: .25px solid #ddd;
        border-bottom: .25px solid #ddd;
        padding-top:0.3rem ;
        padding-bottom: 0.3rem;
      }
      .img2{
        width: .9rem;
        /*height: .43rem;*/
        padding: .5rem .1rem .3rem .3rem;
      }
      span{
        vertical-align: .35rem;
        font-size: 0.6rem;
        padding-bottom: 0.2rem;
      }
      .van{
        font-size: .7rem;
        color: #e4e4e4;
        padding-right: .22rem;
        padding-top: .3rem;
      }
      .span3{
        position: absolute;
        right: .6rem;
      }
      .btn{
        width: 100%;
        background: #f2f2f2;
      }
      .btn1{
        z-index: 5;
        width: 96%;
        margin: .7rem 2% ;
        line-height: .7rem;
        border-radius: .13rem;
        text-align: center;
        background: #d8584a;
        font-size: .7rem;
        color: #fff;
      }
      .bottom{
        width: 74%;
        background-color: #fff;
        text-align: center;
        position: absolute;
        left: 13%;
        top: 2.5rem;
        border-radius:.2rem ;
        .van4{
          padding-top: .2rem;
          font-size: 1.5rem;
          color: #f8cb86;
        }
        h4{
          font-size: .3rem;
          color: #333;
          line-height: .4rem;
          margin-top: .3rem;
          padding-bottom: .3rem;
        }
        .btn3{
          text-align: center;
          font-size: .4rem;
          color: #fff;
          font-weight: 700;
          margin-top: .4rem;
          background-color: #4cd964;
          width: 100%;
          line-height: .9rem;
          border-radius: 0 0 .2rem .2rem;
        }
      }
    }

  }

</style>



