<template>
    <div class="balan">
      <van-nav-bar
        title=""
        left-arrow
        @click-left="onClickLeft"
      />
      <div class="all">

        <section class="kuang">
          <header>
            <span class="yue"></span>
            <section>
              <img src="data:image/png;base64," alt="" class="img">

              <router-link :to="{path:'/explain'}" class="shuoming"></router-link>
            </section>
          </header>


          <!---->
          <p>
            <span class="top">0.00</span>
            <span class="bott"></span>
          </p>

          <van-button type="primary" size="large" class="btn"></van-button>
        </section>

      </div>

      <p class="p1"></p>

      <div class="log">
        <img src="data:image/png;base64," alt="" class="img1">
        <p class="p2"></p>
      </div>
    </div>
</template>

<script>
    export default {
        name: "balance",
      methods:{
        onClickLeft(){
          this.$router.go(-1);
        }
      }
    }
</script>

<style scoped>
  .balan{
    position: relative;
  }
  .van-nav-bar {
    background-color: #3190e8;
  }
  .van-nav-bar__title {
    color: white;
    font-weight: 400;
  }
  :before{
    color: white;
    text-align: center;
  }
  .all{
    width: 100%;
    height: 7.5rem;
    background: #3190e8;
    margin: 0;
    box-sizing: border-box;
    float: left;
  }
.kuang{
  width: 94%;
  height: 90%;
  background: white;
  margin-left: 0.5rem;
  margin-top: 0.25rem;
  border-radius: .2rem;
  position: relative;
}

  .van-hairline--bottom::after {
    border-bottom-width: 0rem;
  }
  .img{
    width: 0.6rem;
   position: absolute;
    right: 3.2rem;
    top: 0.6rem;
  }
  .yue{
    font-size: 0.6rem;
    color: #666;
    margin-left:0.5rem ;
  }
  .shuoming{
    font-size: 0.6rem;
    color: #3190e8;
    position: absolute;
    right: 0.5rem;
    top: 0.5rem;
  }
  .top{
  font-size: 1.8rem;
    margin-left:0.7rem;
  }
  .bott{
    font-size: 0.7rem;
  }
  .btn{
    background: #ccc;
    border: .1rem solid #ccc;
    width: 94%;
    margin-left: 2%;
    margin-top: 0.5rem;
    border-radius: 0.1rem;
  }
  .p1{
    position: absolute;
    color: #999;
    font-size: .6rem;
    top: 10rem;
    left: .6rem;
  }
  .img1{
    width: 8rem;
  }
  .p2{
    color: #999;
    font-size: .7rem;
    margin-top: .5rem;
  }
  .log{
    text-align: center;
  position: absolute;
    left: 0;
    top: 12rem;
    right: 0;
    bottom: 0;
  }
</style>



