<template>
    <div class="member">
      <van-nav-bar
        title=""
        left-arrow
        @click-left="onClickLeft"
      />
      <div class="all">
        <p class="p">
        <span class="span">{{userinfo.username}}</span>
          
        </p>
        <div class="vip">
          <router-link :to="{path:'/sale'}">
            <span class="span1"></span>
            <span class="span2"><i class="iconfont">&#xe714;</i></span>
          </router-link>
          <div class="center">
            <img src="../imags/e72f0e5a3d4d2cbb3a53b316019c96e.png" alt="" class="img">
            <div class="div1">
              <h4></h4>
              <p class="p1">3034</p>
              <p class="p1"></p>
            </div>

          </div>


          <div class="center">
            <img src="../imags/58a495e68c5e3348e6bcb82580f244d.png" alt="" class="img">
            <div class="div1">
              <h4></h4>
              <p class="p1">3034</p>
              <p class="p1"></p>
            </div>

          </div>

        </div>

        <!---->
        <div class="open">
          <div class="open1"></div>
          <div class="open2">
            <div class="open3">
              <span class="span3"></span>
              <span class="span4">20</span>
            </div>
            <router-link :to="{path:'/countdown1'}">
              <div class="open4" ></div>
            </router-link>

          </div>
        </div>
        <div class="bottom">
          <router-link :to="{path:'/member_c'}">
            <span class="span1"></span>
            <span class="span2"><i class="iconfont">&#xe714;</i></span>
          </router-link>
        </div>

        <div class="bottom">
          <router-link :to="{path:'/invoice'}">
            <span class="span1"></span>
            <span class="span2"><i class="iconfont">&#xe714;</i></span>
          </router-link>
        </div>

      </div>
    </div>
</template>

<script>
  import {mapActions,mapState} from "vuex"
    export default {
        name: "member",
      methods:{
        ...mapActions(['SET_USER_INFO']),
        onClickLeft(){
          this.$router.go(-1);
        }
      },
      computed:{
        ...mapState(['userinfo']),
      },
    }
</script>

<style scoped>
  .van-nav-bar {
    background-color: #3190e8;
  }
  .van-nav-bar__title {
    color: white;
    font-weight: 400;
  }
  :before{
    color: white;
    text-align: center;
  }
  .p{
    font-size: .6rem;
    color: #666;
    line-height: 2rem;
    padding-left: .7rem;
  }
  .span{
   color: #333;
    font-weight: 700;
  }
  .vip{
    background: white;
    padding-left: .7rem;
    margin-bottom: .5rem;
    line-height: 2rem;
    border-bottom: 1px solid #f5f5f5;
    padding-right: .7rem;
  }
  .span1{
    font-size: .7rem;
    color: #333;
  }
    .span2{
      float: right;
      font-size: .6rem;
      color: #999;
      margin-right: .2rem;
      margin-top: 0.1rem;
    }
  .center{
    border-top: 1px solid #f5f5f5;
    padding: .8rem 0;
    position: relative;
  }
  .img{
    width: 1.6rem;
    margin-right: .6rem;
  }
h4{
  font-size: .7rem;
  color: #333;
  font-weight: 400;
  line-height: 0.8rem;
}
.p1{
  font-size: .5rem;
  color: #999;
  line-height: 0.6rem;
}
  .div1{
    padding: 0;
    margin: 0;
    position: absolute;
    left: 2.2rem;
    top: 0.9rem;
  }
  .open{
    background: white;
    padding-left: .7rem;
    margin-bottom: .5rem;
  }
  .open1{
    font-size: .7rem;
    color: #333;
    line-height: 2rem;
    border-bottom: 1px solid #f5f5f5;
    padding-right: .7rem;
  }
  .open2{
    padding-right: .7rem;
    font-size: .7rem;
    line-height: 2.6rem;
    position: relative;
  }
  .open3{
    color: #333;
    font-weight: 400;

  }
  .open4{
    border: .025rem solid #f60;
    border-radius: .2rem;
    line-height: 1.2rem;
    height: 1.2rem;
    width: 2.6rem;
    text-align: center;
    font-size: .6rem;
    color: #f60;
    position: absolute;
    right: 0.7rem;
    top: .6rem;

  }
  .span4{
    font-weight: 700;
    color: #f60;
  }
  .bottom{
    background: white;
    padding-left: .7rem;
    margin-bottom: .5rem;
    line-height: 2rem;
    border-bottom: 1px solid #f5f5f5;
    padding-right: .7rem;
  }
</style>



