<template>
  <div class="conversion">
    <!---->
    <van-nav-bar
      title=""
      left-text=" "
      arrow-left
      left-arrow
      @click-left="onClickLeft"
    />

    <!---->
    <div class="content">

      <input type="text" placeholder="" v-model="exchangecode" class="input" >

     <div class="div1">
       <input type="text" placeholder="" v-model="captchacode" class="input1" maxlength="4">
       <!---->
       <div class="yzm">
         <img :src=code1 alt="">
         <div @click="look" class="div2">
           <p class="p"></p>
           <p class="p1"></p>
         </div>

       </div>

     </div>
    </div>
      <div class="convert" @click="exchange" :class="{convert1:cheack1}"></div>

    <transition name="fade" enter-active-class="animated bounceIn"
                :duration="400">
      <Pop_c v-if="show" :popKuang="popKuang"></Pop_c>
    </transition>

  </div>
</template>

<script>
 import {postconversion,yzmjmx} from "../serivice/api";
 import {mapActions,mapState} from "vuex"
 import Pop_c from "../components/Cpm_c/Pop_c"
  export default {
    name: "conversion",
    components:{Pop_c},
    data(){
      return{
        code1:"",
        allInfo1:[],
        exchangecode:"",
        userid:"",
        captchacode:"",
        show:false,
        popKuang:"",
        cheack1:false,
      }
    },
    watch:{
      exchangecode(){
        if(this.exchangecode.length>0 && this.captchacode.length==4){
          this.cheack1=true;
        }else{
          this.cheack1=false;
        }
      },
      captchacode(){
        if(this.exchangecode.length>0 && this.captchacode.length==4){
          console.log(1);
          this.cheack1=true;
        }else{
          this.cheack1=false;
        }
      }

    },
    methods: {
      ...mapActions(['SET_USER_INFO']),
      btntxt_c(show){
        this.show = show;
      },

      exchange(){
        postconversion({
          user_id:this.userinfo.user_id,
          exchange_code:this.exchangecode,
          captcha_code:this.captchacode,
        }).then((result)=>{
            console.log(result);
          if(result.message == ""){
            this.show = true;
            this.popKuang = "";
            return;
          }
        });
      },
      onClickLeft() {
        this.$router.push({path:'/discounts'});
      },
      look(){
        yzmjmx().then((result)=>{
          this.code1=result.code
        })
      },
    },
    created(){
      //
      yzmjmx().then((result)=>{
        this.code1=result.code
      });

    //  
    //     if (this.exchangecode.length>0 || this.captchacode.length=4){
    //           this.cheack1=true;
    //     } else{
    //       this.cheack1=false;
    //     }
    },
    computed:{
      ...mapState(['userinfo']),

    },
  }
</script>

<style scoped lang="less">

  .van-nav-bar {
    background-color: #3190e8;
  }
  .van-nav-bar__title {
    color: white;
    font-weight: 400;
  }
  :before{
    color: white;
    text-align: center;
  }
  .content {
    margin-top: .7rem;
    padding: 0 .5rem;
  }
    input{
      border: 0.1px solid grey;
    }

    .input{
      width: 100%;
      font-size: .7rem;
      color: #666;
      padding: .6rem .4rem;
      border-radius: .15rem;
    }
    .input1{
      font-size: .7rem;
      color: #666;
      padding: .4rem;
      border-radius: .15rem;
    }
    .p{
      font-size: .25rem;
      color: #666;
    }
  .p1{
    color: #3b95e9;
    font-size: 0.2rem;
    z-index: 10;
  }
    .convert{
      background-color: #ccc;
      font-size: .7rem;
      color: #fff;
      text-align: center;
      margin: .7rem .7rem;
      line-height: 1.8rem;
      border-radius: .2rem;
    }
  .convert1{
    background-color: #4cd964;
    font-size: .7rem;
    color: white;
    text-align: center;
    margin: .7rem .7rem;
    line-height: 1.8rem;
    border-radius: .2rem;
  }
  /**/
  .yzm {
    width: 100%;
    font-size: .7rem;
    color: #666;
    padding: .4rem;
    border-radius: .15rem;
    position: relative;
    background: white;
    margin-left: 0.5rem;
  }
    img {
      width: 3.5rem;
      height: 1.5rem;
      margin-right: .2rem;
      display: flex;
      position: absolute;
      right: 2.5rem;
    }
  .div1{
    height: 2.2rem;
    display: flex;
    flex: 2;
    margin-top: .7rem;
  }
  .div2{
    display: flex;
    flex-wrap: wrap;
    width: 2rem;
    justify-content: center;
    position: absolute;
    top: 0.5rem;
    right: 0.2rem;
  }
</style>



