<template>
    <div class="discounts">
      <van-nav-bar
        title=""
        left-arrow
        @click-left="onClickLeft"
      />

      <van-tabs sticky>
        <van-tab title="">
          <div class="kuang">
            <span class="yue">
            <span style="color: red;">{{arrayH.length}}</span>
              
            </span>
            <section>
              <img src="data:image/png;base64," alt="" class="img">

              <router-link :to="{path:'/explain_hb'}" class="shuoming"></router-link>
            </section>
          </div>

          <ul>
            <li v-for="(value,index) in newarrayH" :key="index">
              <section class="all">
                <!---->
                <div class="div1">
                  <span class="span1"></span>
                  <span class="span2">{{value.numberMoney.num1}}</span>
                  <span class="span3">.</span>
                  <span class="span4">{{value.numberMoney.num2}}</span>
                  <p class="p1">{{value.sum_condition}}</p>
                </div>

                <!---->
                <div class="div2">
                  <p class="p2">{{value.name}}</p>
                  <p class="p3">{{value.end_date}}</p>
                  <p class="p4"> {{value.phone}}</p>
                </div>

                <!---->
                <div class="div3">
                 {{value.description_map.validity_delta}}
                </div>
              </section>
              <div class="bottom">
                <p class="p">{{value.limit_map==undefined? "":value.limit_map.restaurant_flavor_ids}}</p>
              </div>
            </li>

          </ul>
          <div class="link">
            <router-link :to="{path:'/history'}"><span class="s1"> ></span></router-link>
          </div>
            <div class="div4">
              <div >
                <router-link :to="{path:'/conversion'}"><span class="ss"></span></router-link>
              </div>

              <div>
                <router-link :to="{path:'/awardB_c'}"><span class="ss"></span></router-link>
              </div>
            </div>
        </van-tab>
          <van-tab title="">
            <div class="kuang">
              <section>
                <img src="data:image/png;base64," alt="" class="imgs">

                <router-link :to="{path:'/explain_ye'}" class="shuoming"></router-link>
              </section>
              
              <div class="pri">
                <img src="data:image/png;base64," alt="" id="img">
                <p class="p-1"></p>
                <p class="p-2"></p>
                <router-link :to="{path:'/past'}" class="btn">
                  <span class="s-1"></span>
                </router-link>
              </div>
            </div>

          </van-tab>
      </van-tabs>
    </div>
</template>

<script>
  import {mapActions,mapState} from "vuex"
  import {getdiscounts} from "../serivice/api"
    export default {
        name: "discounts",
      data(){
          return{
            arrayH:[],
            allinfo:[],
        };
      },
      methods:{
        ...mapActions(['SET_USER_INFO']),
        onClickLeft(){
          this.$router.push({path:'/minejmx'});
        },

      },
      mounted(){
        // this.allinfo=JSON.parse(localStorage.getItem('allinfo'));
        // console.log(this.allinfo.user_id);
        getdiscounts(this.userinfo.user_id).then((result)=>{
          this.arrayH=result
          console.log(this.newarrayH)
        });



      },
      computed:{
        ...mapState(['userinfo']),
        newarrayH(){
          this.arrayH.forEach((item)=>{
            let nn =item.amount.toString().split(".");
            if(nn[1]==undefined){
              nn[1]="0";
            }
            item.numberMoney={num1:nn[0],num2:nn[1]};
          });
          return this.arrayH;
        }

      },

    }
</script>

<style scoped>
  .van-nav-bar {
    background-color: #3190e8;
  }
  .van-nav-bar__title {
    color: white;
    font-weight: 400;
  }
  :before{
    color: white;
    text-align: center;
  }
  .kuang{
    /*width: 94%;*/
    /*height: 90%;*/
    margin-left: 0.5rem;
    margin-top: 0.25rem;
    position: relative;
  }
  .img{
    width: 0.6rem;
    position: absolute;
    right: 3.2rem;
    top: 0.6rem;
  }
  .yue{
    font-size: 0.55rem;
    color: #666;
    margin-left:0.5rem ;
  }
  .shuoming{
    font-size: 0.6rem;
    color: #3190e8;
    position: absolute;
    right: 0.5rem;
    top: 0.5rem;
  }
  .div1{
    font-size: 0;
    border-right: 0.1rem dotted #ccc;
    padding-right:.5rem ;
    position: absolute;
    left: .7rem;
    top: 0.7rem;
  }
  .div2{
    font-size: 0;
    margin-left: .5rem;
    position: absolute;
    left: 4.5rem;
    top: 0.9rem;
    line-height: .8rem;
  }
.span1{
  font-size: .9rem;
  color: #ff5340;
  font-weight: 900;
}
  .span2{
    font-size: 1.5rem;
    color: #ff5340;
  }
  .span3{
    font-size: .8rem;
    color: #ff5340;
    font-weight: 900;
  }
  .span4{
    font-size: .8rem;
    color: #ff5340;
    font-weight: 900;
  }
  .p1{
    font-size: .4rem;
    color: #999;
    letter-spacing:.1rem;
  }
  li{
    background: white;
  }
  .p2{
    font-size: .7rem;
    color: #666;
  }
  .p3,.p4{
    font-size: .4rem;
    color: #999;
  }
  .div3{
    font-size: .75rem;
    color: #ff5340;
    position: absolute;
    top: 0.9rem;
    right: .4rem;

  }
  .all{
    margin-top: 0.7rem;
    padding: 2rem .5rem;
    position: relative;
  }
  li{
    width: 92%;
    border-radius:2%;
    background: white url("../imags/123.jpg") repeat-x;
    background-size: .5rem .2rem;
    margin: 0.5rem 4%;
  }
  .bottom{
    background-color: #f9f9f9;
    padding:0 .4rem;
    border-bottom-left-radius: .25rem;
    border-bottom-right-radius: .25rem;
  }
  .p{
    list-style-type: disc;
    font-size: .4rem;
    color: #999;
    margin-left: .4rem;
    line-height: 1.2rem;
  }
  .link{
    padding: .5rem 0 2.3rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .s1{
    font-size: .5rem;
    color: #999;
    margin-right: .2rem;
  }
  .div4{
    width: 100%;
    line-height: 2rem;
  }
  .div4>div{
   float: left;
    width: 50%;
    text-align: center;
    background: white;
    border-right: 0.1rem;

  }
  .ss{
    font-size: .7rem;
    color: #555;
  }
  .imgs{
    width: 0.6rem;
    position: absolute;
    right: 4.8rem;
    top: 0.6rem;
  }
  .pri{
    padding-top: 7rem;
    text-align: center;
  }
  #img{
    width: 50%;
  }
  .p-1{
    color: #666;
    font-size: .9rem;
    margin-top: 0.4rem;

  }
  .p-2{
    color: #999;
    font-size: 0.7rem;
    margin: 0.4rem;
  }
  .s-1{
    display: inline-block;
    font-size: .9rem;
      color: white;
      background: #56D176;
    padding: .5rem;
    margin-top: 0.5rem;
    border-radius: 5%;
  }
</style>



