<template>
    <div class="explain">
      <van-nav-bar
        title=""
        left-arrow
        @click-left="onClickLeft"
      />
      <h3>Q1: </h3>
      <p></p>

      <h3>Q2: </h3>
      <p>2016122500:00</p>

      <h3>Q3:</h3>
      <p>15050</p>

      <h3>Q4:</h3>
      <p></p>
    </div>
</template>

<script>
    export default {
        name: "explain",
      methods:{
        onClickLeft(){
          this.$router.go(-1);
        }
      }
    }
</script>

<style scoped>
  .van-nav-bar {
    background-color: #3190e8;
  }
  .van-nav-bar__title {
    color: white;
    font-weight: 400;
  }
  :before{
    color: white;
    text-align: center;
  }
  .explain{
    background: white;
    height: 100%;
  }
  h3{
    padding: .5rem;
    font-size: .7rem;
    color: #333;
  }
  p{
    /*padding-top: .1rem;*/
    /*padding-bottom: .1rem;*/
    padding-left: .5rem;
    font-size: .6rem;
    color: #666;
    line-height: 1rem;
  }
</style>



